html {
	box-sizing: border-box;
	font-size: 16px;
}


*,
*::before,
*::after {
	box-sizing: inherit;
	
}

html,
body {
	height: 100%;
	position: relative;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

button:focus,
input:focus {
	outline: transparent;
}

h5 {
	font-size: 20px;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.container {


}
/* основные заголовки */
.wrapper-decr{
	.info{
		display: block;
		font-family: "Roboto-Thin", serif;
		font-size: 11px;
		text-align: center;
		text-transform: uppercase;
		width: 112px;
		height: 24px;
		background-color: #000000;
		color: #fff;
		margin: 0 auto 15px;
		text-align: center;
		
	
	}
	.title{
		font-family:"Roboto-Bold", serif;
		text-align: center;
		text-transform: uppercase;
		font-size: 38px;
		color: #222;
		margin-bottom: 19px;
	
	}
	
	.subtitle{
		font-size: 17px;
		line-height: 21px;
		font-weight: 400;
		color: #222222;	
		text-align: center;	
		margin-bottom: 32px;
	}
}

.btn{
	width: 172px;
	height: 46px;
	border: 2px solid #F64C2B;
	border-radius: 5px;
	box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .2em rgb(2, 2, 2);
	border: 3px solid rgba(0, 0, 0, 0.07);
	background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
	margin: 19px 0;
	font-size: 14px;
	color: #fff;
	text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.21);
	text-transform: uppercase;
	font-weight: 400;
	
	
	&:hover{
		width: 172px;
		height: 46px;
		border: 2px solid #F64C2B;
		border-radius: 5px;
		box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .2em rgb(2, 2, 2);
		border: 3px solid rgba(0, 0, 0, 0.07);
		background-image: linear-gradient(to top, #114a55 0%, #4ce2ff 100%);
		margin: 19px 0;
		font-size: 14px;
		color: #fff;
		text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.21);
		text-transform: uppercase;
		font-weight: 400;
		transition: all 0.3s;
	}
}
.burger{
	display: none;
}
