//MEDIA: 767
@media screen and (max-width: 767px) {
    .menu{
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, 0.8);
        transition: 0.6s;
        &__active{
            left: 0;
        }
            &__item{
                padding-right: 0;
                border: none;
                margin: 10px 0;
            }
            &__link{
                font-size: 20px;
            
            }
    }
    nav{
        position: absolute;
    }
    .burger{
        display: block;
        position: absolute;
        right: 40px;
        top: 50%;
        transform: rotateY(-50%);
        width: 26px;
        height: 12px;

            span{
                display: block;
                height: 2px;
                width: 100%;
                background-color: #fff;
                margin-bottom: 2px;
                transition: .5s all;
            }
            &__active{
                span{
                    margin-bottom: -2px;
                    &:nth-child(1){
                        transform: translateY(3px) rotate(-45deg);
                    }

                    &:nth-child(2){
                        display: none;
                        
                    }
                    &:nth-child(3){
                        transform: translateY(3px) rotate(45deg);
                    }
                }
            }

    }
    .col-hidden{
        display: none;
    }
    .xs-visivle{
        display: block;
    }

.subheader__tel{
    text-align: center;
    font-size: 20px;
}
.subheader__btn{
    margin: 10px 0;
}

.choice{
    height: 528px;
    padding: 13px 0;
    &__img{
        top: 63%;
        img{
            width: 100%;
        }
    }
}
.subimg{
    margin: 200px;
}
.onworld{
    background: url(../img/bg/word-bg.png)50% 0/ no-repeat ;
}


}

