@import 'mixins';
@import 'vars';
@import 'local-fonts';
@import 'fonts';
@import 'global';
@import 'nullstyle';
@import 'svg';
@import 'bem-blocks';


/* NOTE - import here your style modules */
header{
	height: 50px;
}
nav {
	background-color: #212121;
	height: 50px;
	position: sticky;
	top: 0;
	position: fixed;
	width: 100%;
	z-index: 1;

}

.menu {
	padding: 15px 70px;
	display: flex;
	justify-content: space-between;
		&__item {
			border-right: 2px dotted black;
			padding-right: 30px;
			&:last-child{
				border: none;
			}
		}

		&__link {
			color: #ffffff;
			font-size: 15px;
			font-weight: 300;
		}
}
.subheader {
	position: absolute;
	width: 100%;
	background-color: rgba(0, 0, 0, 0.4);
	height: 84px;
	margin-top: 50px;

	&__logo{
		max-width: 100%;
		display: inline-block;
		margin-top: 16px;
	}
	&__official{
		color: #ffffff;
		font-size: 13px;
		font-weight: 100;
		opacity: .5;
		margin-top: 6px;
	}
	&__call{
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 23px;
		margin-left: 20px;
	}
	&__tel{
		display: block;
		color: #ffffff;
		font-weight: 400;
		font-size: 22px;
		margin-top: 8px;

	}

}


.promo {
	padding: 150px 0 135px;
	height: 800px;
	background: url(../img/bg/woman-bg.jpg) 0 0/cover no-repeat ;
	&__item{
		color: #FFFFFF;
		text-align: center;
	}

	&__header {
		text-transform: uppercase;
		font-size: 60px;
		font-family: "Roboto-Black", serif;
		margin-bottom: 24px;
	}

	&__subheader {
		font-size: 36px;
		font-family:"Roboto-Bold", serif;
		text-transform: uppercase;
		margin-bottom: 24px;

		
	}

	&__decr {
		font-size: 18px;
		font-family:"Roboto-Medium", serif;
		margin-bottom: 118px;
	}

 

}



.cause {
	display: flex;
	justify-content: center;
	align-items: center;
	padding: 65px 0;



		&__item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-top: 35px;
		}

		&__circ {
			width: 113px;
			height: 113px;
			background-color: #1eacc7;
			border-radius: 100%;
			margin-right: 25px;
		}

		&__descr {
			width: 340px;
		}

		&__subtitle {
			color: #1eacc7;
			font-size: 17px;
			font-weight: 300;
			margin-bottom: 10px;

		}

		&__text {
			font-size: 15px;
			font-weight: 100;
		}
		
}


/* мобил */
.mobile{
	min-height: 864px;
	padding: 48px 0 79px;
	background: url(../img/bg/bg-skv.jpg) 0 0/ repeat ;
	&__item-subtitle{
		font-size: 24px;
		color: #fff;
		font-weight: 400;
		text-transform: uppercase;
		text-align: center;
	}
	&__item-plus{
		cursor: pointer;
		position: absolute;
		bottom: 35px;
		left: 0;
		right: 0;
		width: 26px;
		height: 26px;
		background-color: #000000;
		border-radius: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
		padding: 6px;
		&::before{
			content: '';
			width: 14px;
			height: 2px;
			background-color: $color1;
			
		}
		&::after{
			content: '';
			position: absolute;
			height: 14px;
			width: 2px;
			background-color: $color1;

		}


		
	}

	&__item{
		width: 320px;
		height: 280px;
		margin: 19px auto 0;
		padding-top: 92px;
		position: relative;
		&-1{
			background: url(../img/layers/layers1.jpg) 0 0/cover no-repeat ;
		}
		&-2{
			background: url(../img/layers/layers2.jpg) 0 0/cover no-repeat ;
		}
		&-3{
			background: url(../img/layers/layers3.jpg) 0 0/cover no-repeat ;
		}
		&-4{
			background: url(../img/layers/layers4.jpg) 0 0/cover no-repeat ;
		}
		&-5{
			background: url(../img/layers/layers5.jpg) 0 0/cover no-repeat ;
		}
		&-6{
	background: url(../img/layers/layers6.jpg) 0 0/cover no-repeat ;			
		}
	}
}




.info {
	display: block;
	font-family: "Roboto-Thin", serif;
	font-size: 11px;
	text-align: center;
	text-transform: uppercase;
	width: 112px;
	height: 24px;
	background-color: #fff;
	color: #222;
	margin: 0 auto 15px;
	text-align: center;
	

}
.title-whait {
	font-family:"Roboto-Bold", serif;
	text-align: center;
	text-transform: uppercase;
	font-size: 38px;
	color: #fff;
	margin-bottom: 19px;
	
}
.subtitle-whait {
	font-size: 17px;
	font-weight: 400;
	color: #fff;	
	text-align: center;	
	margin-bottom: 32px;
	
}

/* Возможности */
.choice {
	position: relative;
	padding: 57px 0 138px;
	background:
	url(../img/bg/black_bg.jpg)0 0 /100% no-repeat ,
	url(../img/bg/bg-skv.jpg) -380px 0/100% repeat ;
	height: 900px;

	&__item {
		color: #fff;
		display: flex;
		justify-content: center;align-items: center;
		z-index: 100;
	}

	&__img {
		position: absolute;
		margin: 0;
		top: 50%;
		right: 50%;
		transform: translate(50%, -55%);
		z-index: 1;

	}


}

.subimg{
	color: #222222;
	margin-top: 500px;
	text-align: center;
	font-size: 17px;
	font-weight: 300;
	
}



.require{
	padding: 68px 0 84px;
	&__block{
		margin: 33px 0;

	}
	&__title{
		font-size: 22px;
		font-weight: 400;
		color: #1ca8c3;
		margin-bottom: 30px;
	}
	&__list{
		padding-left: 50px;
		li{
			cursor: pointer;
			margin-bottom: 20px;
			font-size: 17px;
			font-weight: 300;
			color: #222;
			&::before{
				content: '';
				display: block;
				width: 21px;
				height: 20px;
				background: url(../img/icon/proba.png);
				position: absolute;
				left: 20px;

			}

		}
	}
	&__desr{
		color: #1ca8c3;
		font-size: 17px;
		font-weight: 300;
	}

}
.nbm{
	margin-bottom: 0;

}

.require__title-red{
	color: red;
}

.red{
	
	li{
		&::before{
			content: '';
			display: block;
			width: 21px;
			height: 20px;
			background: url(../img/icon/okred.png) 0 0/cover no-repeat ;
			position: absolute;
			left: 20px;

		}
		}

}


.onworld{
	position: relative;
	padding: 49px 0px;
	height: 717px;
	background: url(../img/bg/word-bg.png) 0 0/cover no-repeat ;


	a{
		position: absolute;
		display: block;
		bottom: 49px;
		justify-content: center;
		left: 0;
		right: 0;
		text-align: center;
		color: $color1;
		text-transform: uppercase;
		
	}
}


.footer {
	padding: 50px 23px 39px 23px;
	height: 148px;
	&__wrapper {
		display: flex;
		justify-content: space-between;
		
	}
	&__wrapper >div{
		width: 33%;
		&:nth-child(3){
			display: flex;
			justify-content: flex-end;
		}

	}

	&__social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		


	}

	&__social-item {
		width: 40px;
		height: 40px;
		border-radius: 100%;
		background-color: #070716;
		display: flex;
		justify-content: center;
		align-items: center;
		i{
			font-size: 25px;
			color: #fff;

		}
	}

	&__links {
		display: inline-block;
		width: 550px;
		margin: 0 auto;
		text-align: center;
		align-items: center;
		a{
			color: #070716;
		}
	}

	&__links-main {
		display: flex;
		justify-content: space-around;
		font-size: 15px;
		font-weight: 300;
		text-transform: uppercase;
		margin: 0px 0 15px 0;
		

	}

	&__links-sub {
		display: flex;
		justify-content: space-between;
		font-size: 11px;
		font-weight: 300;
		text-transform: uppercase;
		margin-bottom: 15px;
		a{
			color: #999;
		}
	}
	&__links-lengv{
		text-align: center;
		text-transform: uppercase;
		font-size: 11px;
		visibility: 400px;
			a{
				color: $color1;
			}
	}
	&__app-img{
		display: flex;
		justify-content: space-between;
		width: 300px;
		a{
			
		}
	}
}

.xs-visivle{
	display: none;
}


